(function() {
  'use strict';

  angular
    .module('CarreEntrySystem')
    .controller('DashboardController', DashboardController);

  /** @ngInject */
  function DashboardController($rootScope, $timeout, toastr, CARRE, $location, CONFIG, $scope,Email) {
    var vm = this;

    //graph init
    vm.countAllInit = 0;
    vm.countAll = 0;
  
    vm.colors=CONFIG.COLORS;
    console.log(vm.colors);
    vm.counterchart_labels = [
      // "Medical Experts",
      "Risk Factors",
      "Risk Evidences",
      "Risk Elements",
      "Observables",
      "Citations"
      // "Measurement Types",
    ];
    vm.counterchart_data=[];
    vm.counterchart_data[0] = [0,0,0,0,0];
    
    //event on create method
    $scope.$on('create', function (event, chart) {
      // set colors for each bar
      if(chart && chart.datasets && chart.datasets[0] && chart.datasets[0].bars.length>0){
        chart.datasets[0].bars.map(function(obj,index){
          obj.fillColor=vm.colors[index];
          obj.strokeColor="#FFF";
          return obj;
        });
      } else {
        Email.bug({title:'chart datasets not exist',data:chart.datasets[0]});
        console.log('chart datasets not exist');
        $timeout(function(){
        chart.datasets[0].bars.map(function(obj,index){
          obj.fillColor=vm.colors[index];
          obj.strokeColor="#FFF";
          return obj;
        });
      },200);
      }
    });

    //get total and unreviewed elements 
    
    CARRE.countAll().then(function(res){
      var r=res.data[0];
      console.log('Counter data:',r);
      
      vm.citations = {
        total:Number(r.citations.value)
      };
      vm.measurement_types = {
        total:Number(r.measurement_types.value)
      };
      vm.observables = {
        total:Number(r.observables.value),
        noreviews:Number(r.observables_unreviewed.value)
      };
      vm.risk_elements = {
        total:Number(r.risk_elements.value),
        noreviews:Number(r.risk_elements_unreviewed.value)
      };
      vm.risk_evidences = {
        total:Number(r.risk_evidences.value),
        noreviews:Number(r.risk_evidences_unreviewed.value)
      };
      vm.risk_factors = {
        total:Number(r.risk_factors.value),
        noreviews:Number(r.risk_factors_unreviewed.value)
      };
      // vm.medical_experts = {
      //   total:Number(r.risk_factors.value),
      //   noreviews:Number(r.risk_factors_unreviewed.value)
      // };
      
      //set data for the graph
      vm.counterchart_data[0][0]=vm.risk_factors.total;
      vm.counterchart_data[0][1]=vm.risk_evidences.total;
      vm.counterchart_data[0][2]=vm.risk_elements.total;
      vm.counterchart_data[0][3]=vm.observables.total;
      // vm.counterchart_data[0][0]=vm.measurement_types.total;
      vm.counterchart_data[0][4]=vm.citations.total;
      
      //set top counter
      vm.countAll = vm.citations.total+
                    vm.risk_elements.total+
                    vm.risk_factors.total+
                    vm.risk_evidences.total+
                    vm.observables.total+
                    vm.measurement_types.total;
                    
                    
      /* Donut graph temp */
    vm.counterdonut_data=[
      vm.risk_factors.total,
      vm.risk_evidences.total,
      vm.risk_elements.total,
      vm.observables.total,
      vm.citations.total
    ];
    vm.counterdonut_labels=vm.counterchart_labels;
    vm.counterdonut_options={};
    
    });

    
  }
})();

angular.module('CarreEntrySystem').service('Citations', function($http, CARRE, CONFIG,QUERY) {

  this.exports={
    'types': getCitationTypes,
    'get': getCitations,
    'save': saveCitation
  };
  
  function getCitations(ArrayOfIDs) { return CARRE.instances('citation',ArrayOfIDs); }
  
  function getCitationTypes() {
    var query="PREFIX risk: <http://carre.kmi.open.ac.uk/ontology/risk.owl#> \n\
              SELECT ?citationType FROM "+CONFIG.CARRE_DEFAULT_GRAPH+" WHERE { ?subject a risk:citation; risk:has_citation_source_type ?citationType}";
    return CARRE.query(query,'no prefix').then(function(res){
      var types=[];
      if(res.data instanceof Array) {
        res.data.forEach(function(obj){
          var type=obj.citationType.value;
          if(type.length>0 && types.indexOf(type)===-1) types.push(type);
        });
      }
      return types;
    });
  }

  function saveCitation(oldElem, newElem, user) {
    console.log('Inserted citation by ',CONFIG.currentUser.graphName);
    user = user || CONFIG.currentUser.graphName;
    var newObj = mapper(newElem);
    console.log('Old: ',oldElem);
    console.log('New: ',newElem);
    console.log('Mapped: ',newObj);
    
    /* invalidate citation_all */
    CARRE.invalidateCache('citation_all');
    CARRE.invalidateCache('count_all');
    
    var updateQuery = "",insertQuery = "";

    if (oldElem.id) {
      /*Update query*/
      updateQuery = QUERY.update(oldElem,newObj);
      console.log('----Update Query----');
      return CARRE.query(updateQuery,'no prefix');
    }
    else {
      /*Insert query*/
      insertQuery = QUERY.insert(newObj,"citation","CI",user);
      console.info('-----insertQuery------');
      return CARRE.query(insertQuery,'no prefix');
    }

  }
  
  //map the final object to rdf properties
  function mapper(obj){
    return {
      has_citation_source_type: {pre:'risk',value:obj.type.toString(),type:"string"},
      has_citation_source_level: {pre:'risk',value:obj.level.toString(),type:"integer"},
      has_citation_summary: {pre:'risk',value:obj.summary.toString(),type:"string"},
      has_citation_pubmed_identifier: {pre:'risk',value:obj.pubmedId.toString(),type:"string"}
    };
  }
  
  return this.exports;
  
});
