angular.module('CarreEntrySystem')
  .directive('carreLinker', function(CONFIG) {
    return {
      templateUrl: 'app/components/linker/linker.html',
      restrict: 'E',
      replace: true,
      scope: {
        'property': '@',
        'model': '='
      },
      controller: function($scope, CONFIG, Medical_experts) {
        
        $scope.show = 'label';
        $scope.items = [];
        var label_arr = [];
        var avoidExpressions = ['has_observable_condition'];
        var arr = $scope.model[$scope.property];
        $scope.label = $scope.model[$scope.property + '_label'];
        
        //handle medical experts
        if ($scope.property === 'has_author' || $scope.property === 'has_reviewer') {
            Medical_experts.get().then(function(res) {
              var USERS={}
              res.data.forEach(function(user){
                USERS[user.has_graph_uri_label]=user;
              });
              
              label_arr = $scope.model[$scope.property + '_label_arr'];
              $scope.show = 'links';
              label_arr.forEach(function(username){
                var user = USERS[username];
                  $scope.items.push({
                    link: (CONFIG.ENV === 'PROD' ? '' : '#/') + "medical_experts/" + user.id_label,
                    label: user.has_firstname_label+' '+user.has_lastname_label
                  });
              })
            });
        } else //handle condition
            if ($scope.property === 'has_observable_condition') {
          $scope.show='condition';
          
          $scope.model['has_risk_evidence_observable'].forEach(function(obj,index){
            var id=obj.substr(obj.lastIndexOf('/')+1);
            var link=(CONFIG.ENV === 'PROD' ? '' : '#/') + 'observables/' + id;
            var label=$scope.model['has_risk_evidence_observable_label_arr'][index];
            //construct <a> tag
            var atag="<a href="+link+">"+label+"</a>";
            //replace all OB_* in the expression
            $scope.label=$scope.label.replace(new RegExp(id, 'g'), atag)
            .replace(new RegExp(">=", 'g'), '&ge;').replace(new RegExp("<=", 'g'), '&le;'); //replace greater than and lower than symbols
          });
          
        }  else //handle external annotations
            if ($scope.property === 'has_external_type' || $scope.property === 'has_external_unit'|| $scope.property === 'has_external_predicate')  {
                $scope.show='external_link';
                $scope.external_link = "<a target='_blank' href='"+$scope.model[$scope.property]+"'>"+$scope.model[$scope.property+'_label']+"</a>";
        }
        else {
          // handle links
          if (arr instanceof Array && avoidExpressions.indexOf($scope.property) === -1) {
              
            label_arr = $scope.model[$scope.property + '_label_arr'];

            arr.forEach(function(str, index) {
              var id = str.substr(str.lastIndexOf('/') + 1);
              var cat = id.substr(0, 2);
              var type = false;
              switch (cat) {
                case 'CI':
                  // make label for observables
                  type = 'citations/';
                  break;
                case 'OB':
                  // make label for observables
                  type = 'observables/';
                  break;
                case 'ME':
                  // make label for measurent types
                  type = 'measurement_types/';
                  break;
                case 'RF':
                  // make label for risk factor                          
                  type = 'risk_factors/';
                  break;
                case 'RL':
                  // make label for risk element
                  type = 'risk_elements/';
                  break;
                case 'RV':
                  // make label for risk evidence
                  type = 'risk_evidences/';
                  break;
                default:
              }
              if (type) {
                $scope.show = 'links';
                $scope.items.push({
                  link: (CONFIG.ENV === 'PROD' ? '' : '#/') + type + id,
                  label: label_arr[index]
                });
              }

            });
          }
        }
        //debug
        // console.log('Label:',$scope.label,($scope.showlink?'link':'text'));
      }
    };
  });

'use strict';

angular.module('CarreEntrySystem')

.directive('leEditor', ['$compile','$timeout','$q', function($compile,$timeout,$q) {
    return {
        restrict: 'E',
        scope: {
            group: '=',
            observables: '=',
            metypes: '='
        },
        templateUrl: 'app/components/le_editor/le_editor.html',
        compile: function(element, attrs) {
            var content, directive;
            content = element.contents().remove();
            return function(scope, element, attrs) {
                scope.operators = [{
                        name: 'AND'
                    },
                    // { name: 'AND NOT' },
                    {
                        name: 'OR'
                    }
                    // { name: 'OR NOT' }
                ];

                scope.field = {};

                scope.conditions = [
                    {
                        name: '=',  value: '=', types:'integer,float,enum,boolean'
                    },
                    {
                        name: '<',  value: '<', types:'integer,float'
                    }, 
                    {
                        name: '≤',  value: '<=', types:'integer,float'
                    }, 
                    {
                        name: '>',  value: '>', types:'integer,float'
                    }, 
                    {
                        name: '≥',  value: '>=', types:'integer,float'
                    }
                ];

                scope.getMeasurementType = function(item, model, rule) {
                    rule.hiddenData=true;
                    $timeout(function() { 
                        
                        var me_id = '';
                        if (!item) {
                            for (var i = 0; i < scope.observables.length; i++) {
                                if (scope.observables[i].value === model) {
                                    console.log(scope.observables[i])
                                    me_id = scope.observables[i].metype_id;
                                    break;
                                }
                            }
                            me_id = me_id.substring(me_id.indexOf('ME_'));
                            console.log('Get measurement type called!',me_id);
                        }
                        else {
                            me_id = item.metype_id;
                            me_id = me_id.substring(me_id.indexOf('ME_'));
                            console.log('Get measurement type called!',me_id);
                        }
                            
                        for (var i = 0; i < scope.metypes.length; i++) {
                            if (scope.metypes[i].id_label === me_id) {
                                //do stuff
                                rule.datatype = scope.metypes[i].has_datatype_label;
                                rule.unit = scope.metypes[i].has_label_label;
                                rule.unit_label = scope.metypes[i].has_measurement_type_name_label;
                                if (scope.metypes[i].has_enumeration_values_label) rule.dataoptions = scope.metypes[i].has_enumeration_values_label.split(';').map(function(obj) {
                                    return {
                                        value: obj,
                                        name: obj
                                    }
                                });
                                break;
                            }
                        }
                        
                        //make it like loading
                        $timeout(function(){rule.hiddenData=!rule.hiddenData;},400);
                        
                    }, 500);

                };

                scope.addCondition = function() {
                    scope.group.rules.push({
                        condition: '=',
                        field: {
                            selected: {
                                label: ''
                            }
                        },
                        data: ''
                    });
                };

                scope.removeCondition = function(index) {
                    scope.group.rules.splice(index, 1);
                };

                scope.addGroup = function() {
                    scope.group.rules.push({
                        group: {
                            operator: 'AND',
                            rules: []
                        }
                    });
                };

                scope.removeGroup = function() {
                    "group" in scope.$parent && scope.$parent.group.rules.splice(scope.$parent.$index, 1);
                };

                directive || (directive = $compile(content));

                element.append(directive(scope, function($compile) {
                    return $compile;
                }));
            }
        }


    }
}]);

