angular.module('CarreEntrySystem').service('RdfFormatter', function(CONFIG, CarreTranslate, Email) {

  this.exports = {
    'groupByProp': groupByProp,
    'mappings': replaceMappings,
    'translate':translate
  };

  /* =====================Simple groupby prop============================================ */

  /*
  You can configure triplet variable names and group by index of property.
  e.g groupByProp(data,["citation","relation","value"],0).data groups by citation
  */
  function groupByProp(data, triplesFormat, propIndex, valueProp) {
    triplesFormat = triplesFormat || ['subject', 'predicate', 'object'];
    var settingsObj = {
      triplesFormat: triplesFormat,
      groupProp: triplesFormat[(propIndex || 0)],
      valueProp: valueProp,
      keys: [],
      fields: [],
      mappings: {},
      data: []
    };                  
                      //reducer           //initial object acts as prev in the reducer
    return data.reduce(tripleAccumulator, settingsObj);
  }   
                            //accumulatedObj , current
  function tripleAccumulator(settings, obj) {
    
    
    
    var type,id, rel, val, val_label;
    
    id = obj[settings.groupProp][settings.valueProp];
    rel = makeLabel(obj[settings.triplesFormat[1]][settings.valueProp]);
    type = obj[settings.triplesFormat[2]].type;
    val = obj[settings.triplesFormat[2]][settings.valueProp];

    /*  Filter educational objects  */
    if (rel === 'has_educational_material') return settings;
    
    //replace greater than and lower than symbols
    if (rel === 'has_observable_condition_text') val = val.replace(new RegExp(">=", 'g'), '≥').replace(new RegExp("<=", 'g'), '≤'); 
    
    /*  Filter BUG-Virtuoso  */
    // if (rel === 'has_risk_evidence_ratio_value' && val==="NAN") {
    //   console.log("BUG-Virtuoso: ",id+': ',val);
    //   Email.bug({
    //     "title": "Risk evidence ratio value "+(settings.data.length>1?"List view":"Single view"),
    //     "element": id,
    //     "predicate": "has_risk_evidence_ratio_value",
    //     "value": val
    //   });
    // }
    
    /*  Filter other languages  */
    if(obj[settings.triplesFormat[2]].hasOwnProperty('xml:lang') && obj[settings.triplesFormat[2]]['xml:lang']!==CONFIG.LANG){
      return settings;
    }
    
    //make labels only for literals (not for nodes or relations)
    if(type==="typed-literal"){ val_label=val; } else { val_label=makeLabel(val);}

    // console.log(type,id,rel,val_label);
    
    //get extra mappings
    if (['CI','OB', 'RF', 'RL', 'RV','ME'].indexOf(val_label.substr(0, 2)) > -1 && val_label.indexOf("observable_condition")===-1) {
      settings.mappings[val_label] = settings.mappings[val_label] || {};
      for (var prop in obj) {
        //except the 3 basic properties
        if (prop !== 'subject' && prop !== 'predicate' && prop !== 'object' && obj[prop].value.length > 0) {
          settings.mappings[val_label][prop] = obj[prop].value;
        }
      }
    }
    
    // dont make labels for external annotations
    if(rel === 'has_external_type' || rel === 'has_external_unit'|| rel === 'has_external_predicate') {
      val_label = makeLabel(val);
    }


    if (settings.fields.indexOf(rel) === -1) settings.fields.push(rel);
    var index = settings.keys.indexOf(id);
    if (index === -1) {
      //then push into the arrays
      settings.keys.push(id);
      settings.data.push({
        "id": id,
        "id_label": makeLabel(id)
      });
      //change index to the last item added
      index = settings.keys.length - 1;
    }
    settings.data[index][rel] = settings.data[index][rel] || [];
    settings.data[index][rel].push(val);
    
    //add label for each value
    // val = (val.indexOf('#') >= 0 ? val.split('#')[1] : val);
    settings.data[index][rel + '_label'] = settings.data[index][rel + '_label'] || '';
    settings.data[index][rel + '_label'] += (settings.data[index][rel + '_label'].length > 0 ? ',' : '') + val_label;
    settings.data[index][rel + '_label_arr'] = settings.data[index][rel + '_label_arr'] || [];
    settings.data[index][rel + '_label_arr'].push(val_label);

    return settings;
  }


  /*========================== HELPERS  ==================================================*/
  function replaceMappings(settings) {
    settings.data=settings.data.map(function(obj) {
      var cat = '';
      for (var prop in obj) {
        if ( prop.indexOf('_label_arr') > 0 && prop.indexOf('has_observable_condition')===-1) {
          //select only props : has_....._label
          obj[prop]=obj[prop].map(function(term) {
            if (settings.mappings.hasOwnProperty(term)) {
              cat = term.substr(0, 2);
              switch (cat) {
                case 'CI':
                  // make label for observables
                  return settings.mappings[term].has_citation_pubmed_identifier;
                case 'OB':
                  // make label for observables
                  return settings.mappings[term].has_observable_name;
                case 'ME':
                  // make label for measurent types
                  return settings.mappings[term].has_measurement_type_name;
                case 'RF':
                  // make label for risk factor    
                  return settings.mappings[term].has_source_risk_element_name +
                    ' ['+ translate(settings.mappings[term].has_risk_factor_association_type) + '] ' +
                    settings.mappings[term].has_target_risk_element_name;
                case 'RL':
                  // make label for risk element
                  return settings.mappings[term].has_risk_element_name;
                case 'RV':
                  // make label for risk evidence
                  return term;

                default:
                  return term;
              }
            } else return translate(term);
            
          });
          obj[prop.split('_arr')[0]]=obj[prop].join(', ');
          
        }
      }
      return obj;
    });
    return settings;
  }

  function translate(str){
    if(!str) return '';
    if(str.indexOf('#') >= 0) return CarreTranslate(str.split('#')[1]);
    else return CarreTranslate(str);
  };
  function makeLabel(str) {
    if(str.indexOf('#') >= 0) {
      str=str.split('#')[1];
      return str;
    } else if(str.indexOf('http') >= 0) {
      return str.substring(str.lastIndexOf('/') + 1);
    } else return str;
  }

  return this.exports;
});
angular.module('CarreEntrySystem').service('QUERY', function(CONFIG) {

  this.exports = {
    'insert': buildInsertQuery,
    'update': buildUpdateQuery,
    'prefix': getPrefix
  };

  /* The prefixes for CARRE*/
  var PREFIXES = {
    'xsd': 'http://www.w3.org/2001/XMLSchema#',
    'rdf': 'http://www.w3.org/1999/02/22-rdf-syntax-ns#',
    'carreUsers': 'https://carre.kmi.open.ac.uk/users/',
    'risk': 'http://carre.kmi.open.ac.uk/ontology/risk.owl#',
    'ME': 'http://carre.kmi.open.ac.uk/measurement_types/',
    'OB': 'http://carre.kmi.open.ac.uk/observables/',
    'RL': 'http://carre.kmi.open.ac.uk/risk_elements/',
    'RV': 'http://carre.kmi.open.ac.uk/risk_evidences/',
    'RF': 'http://carre.kmi.open.ac.uk/risk_factors/',
    'MD': 'http://carre.kmi.open.ac.uk/medical_experts/',
    'CI': 'http://carre.kmi.open.ac.uk/citations/',
    'RW': 'http://carre.kmi.open.ac.uk/reviews/'
  };
  var PREFIXSTR = '';
  var PREFIXARR = [];
  
  function getPrefix(id){
    if(id.indexOf("http")>=0) return id;
    var type = id.split('_')[0];
    return PREFIXES.hasOwnProperty(type)?PREFIXES[type]+id:id;
  }
  
  
  function addPrefix(prefix) {
    if (prefix) {
      if (PREFIXES.hasOwnProperty(prefix)&&PREFIXARR.indexOf(prefix)===-1) {
        PREFIXARR.push(prefix);
        PREFIXSTR += 'PREFIX ' + prefix + ': <' + PREFIXES[prefix] + '> \n';
      }
    }
    else {
      //add all prefixes
      for (var prop in PREFIXES) {
        PREFIXARR.push(prop);
        PREFIXSTR += 'PREFIX ' + prop + ': <' + PREFIXES[prop] + '> \n';
      }
    }
  }

  function keepDiffProps(oldObj,newObj) {
    var obj={};
    for (var prop in newObj) {
      //iterate new object properties
      if( newObj[prop].value instanceof Array ) {
        //if array
        console.log("Array DEBUG: ",prop,newObj[prop].value,oldObj[prop]);
        if(!(oldObj[prop] instanceof Array)) obj[prop]=newObj[prop];
        else if((newObj[prop].value.length!==oldObj[prop].length)) obj[prop]=newObj[prop];
        else {
          for (var i=0,arr=newObj[prop].value;i<arr.length;i++){
            if(oldObj[prop].indexOf(arr[i])===-1) {
            console.log(oldObj[prop],newObj[prop]);
              obj[prop]=newObj[prop];
              break;
            }
          }
        }
      } else if(!oldObj[prop]||oldObj[prop][0]!=newObj[prop].value) obj[prop]=newObj[prop];
    }
    return obj;
  }
  
  function parse(node){
    if(node instanceof Object) {
      //it should be an object
      if(node.type!=="node"){
        if(node.type==="lang") {
          console.log("NODE is ",node);
          return "\"" + node.value + "\"@" + CONFIG.LANG.trim();
        } else return "\"" + node.value + "\"^^" + fillPrefix('xsd:' + node.type);
      } else {
          if (node.value.indexOf('http://') === 0 || node.value.indexOf('https://') === 0) return '<' + node.value + '>';  //if it is node without prefix
          else return fillPrefix(node.value); //if it is node or relation with prefix
      }
    } else {
      if (node.indexOf('http://') === 0 || node.indexOf('https://') === 0) return '<' + node + '>';  //if it is node without prefix
      else return fillPrefix(node); //if it is node or relation with prefix
    }
  }
  
  function fillPrefix(test, prefixes) {
    if(test.indexOf(":")===-1) test = "risk:"+test;
    var val = test.split(':');
    if(CONFIG.OPTIONS.usePrefix) {
      addPrefix(val[0]);
      return test;
    } 
    prefixes = prefixes || PREFIXES;
    if (prefixes.hasOwnProperty(val[0])) return '<' + prefixes[val[0]] + val[1] + '>';
    else return '<node_' + Math.round(Math.random() * 100000000) + '>';
  }

  /* Update query */
  function buildUpdateQuery (old,obj,id,graph) {
    PREFIXARR=[];
    PREFIXSTR=[];
    graph = graph || CONFIG.CARRE_DEFAULT_GRAPH;
    obj = keepDiffProps(old,obj);
    id = id||parse(old.id);
    var deleteTriples = "";
    var insertTriples = "";
    var objEmpty = true;
    for (var prop in obj) {
      objEmpty = false;
      var pre=obj[prop].pre;
      deleteTriples+= id +" "+ parse(pre+":"+prop) +" ?"+prop+"_var . \n";
      if(obj[prop].value instanceof Array){
        // array e.g observables
        obj[prop].value.forEach(function(elem){
          insertTriples+= id +" "+ parse(pre+":"+prop) +" "+parse(elem)+" . \n" ;
        });
      } else {
        insertTriples+= id +" "+ parse(pre+":"+prop) +" "+parse(obj[prop])+" . \n" ;
      }
    }
    if(objEmpty) return "";
    var deleteQuery = "WITH " + graph + " DELETE { \n" + deleteTriples + " } \n WHERE { \n"+ deleteTriples + " } \n";
    var insertQuery = "INSERT DATA { GRAPH " + graph + " { \n" + insertTriples + " }} \n";
    
    return (CONFIG.OPTIONS.usePrefix?PREFIXSTR:"")+deleteQuery + insertQuery;
  }

  /* Insert query */
  function buildInsertQuery (obj,type,idlabel,usergraph,graph) {
    PREFIXARR=[];
    PREFIXSTR=[];
    graph = graph || CONFIG.CARRE_DEFAULT_GRAPH;
    usergraph = usergraph || CONFIG.currentUser.graphName;
    var insertTriples = "";
    var objEmpty = true;
    for (var prop in obj) {
      objEmpty = false;
      var pre=obj[prop].pre;
      if(obj[prop].value instanceof Array){
        // array e.g observables
        obj[prop].value.forEach(function(elem){
          insertTriples+= "?newid "+ parse(pre+":"+prop) +" "+parse(elem)+" . \n";
        });
      } else {
        //non array
        insertTriples+= "?newid "+ parse(pre+":"+prop) +" "+parse(obj[prop])+" . \n";
      }
    }
    if(objEmpty) return "";
    //add author and type
    insertTriples+= "?newid "+parse("risk:has_author")+ " " + parse(usergraph) + "; a "+parse("risk:"+type)+" .  \n";
    var insertQuery = "INSERT { GRAPH " + graph + " { \n" + insertTriples + " }} WHERE \n\
          { GRAPH " + graph + " \n\
          { { \n\
              SELECT ?oldindex FROM " + graph + " \n\
              WHERE { \n\
               ?elem a "+parse("risk:"+type)+" . \n\
                BIND (xsd:integer(strafter(STR(?elem),\""+idlabel+"_\")) AS ?oldindex) \n\
              } ORDER BY DESC(?oldindex) LIMIT 1 \n\
            } \n\
            BIND (IRI(CONCAT("+parse(idlabel+":")+", \""+idlabel+"_\", ?oldindex+1)) AS ?newid) \n\
          } }";
    
    return (CONFIG.OPTIONS.usePrefix?PREFIXSTR:"")+insertQuery;
  }
      

  return this.exports;
});
angular.module('CarreEntrySystem').service('Pubmed', function($http,CONFIG) {
  var apiEfetch=CONFIG.PUBMED_API_URL;
  var apiSearch=CONFIG.EUROPEPMC_API_URL;
  
  var efetch=function(id){
    return $http.get(apiEfetch+'efetch.fcgi?db=pubmed&retmode=text&rettype=abstract&id='+id,{ignoreLoadingBar: false,cache:true}).then(function(res){
            console.info(res);
            return res;
    },function(err){console.error(err)});
  };
  
  //search Pubmed Central through EuropePMC
  var esearch=function(query){
    return $http.jsonp(apiSearch+'search?resulttype=lite&format=json&callback=JSON_CALLBACK&query='+query,{ignoreLoadingBar: false,cache:true}).then(function(res){
      return { 
        count : res.data.hitCount,
        result : res.data.resultList.result
      };
    },function(err){console.error(err)});
  };
  
  return {
    'fetch':efetch,
    'search':esearch
  };
  
});
