/* global angular */
(function() {
    'use strict';

    angular
        .module('CarreEntrySystem')
        .filter('trustAsResourceUrl', trustResourceFilter)
        .filter('hideSelected', hideSelected)
        .filter('sliceLast', sliceLast)
        .filter('prettySigns', prettySigns)
        .filter('propsFilter', propsFilter)
        .filter('exclude', excludeItems);


    /** @ngInject */
    function trustResourceFilter($sce) {
        return function(val) {
            return $sce.trustAsResourceUrl(val);
        };
    }

    /** @ngInject */
    function hideSelected() {
        return function(item, selectedItems) {
            if(selectedItems.length===0) return false; 
            selectedItems.forEach(function(obj){
                if(item.value===obj) return true;
            });
            return false;
        };
    }
    
    /** @ngInject */
    function sliceLast() {
        return function(item, char) {
            if(!item||item.length<=0||!char||char.length<=0) return ''; 
            return item.substring(item.lastIndexOf(char)+1);
        };
    }
    /** @ngInject */
    function prettySigns() {
        return function(val) {
            if(!val||val.length<=0) return ''; 
            else return val.replace(new RegExp(">=", 'g'), '≥').replace(new RegExp("<=", 'g'), '≤')
        };
    }

    /** @ngInject */
    function excludeItems() {
        return function(items, selectedItems) {
            var out=[];
            if(selectedItems.length===0) return items; 
            //you want to remove the selected items from items
            items.forEach(function(item){
                if(selectedItems.indexOf(item.value)===-1) out.push(item);
            });
            return out;
        };
    }


    /** @ngInject */
    function propsFilter() {
        return function(items, props) {
            var out = [];

            if (angular.isArray(items)) {
                items.forEach(function(item) {
                    var itemMatches = false;

                    var keys = Object.keys(props);
                    for (var i = 0; i < keys.length; i++) {
                        var prop = keys[i];
                        var text = props[prop].toLowerCase();
                        try {
                            if (item[prop].toString().toLowerCase().indexOf(text) !== -1) {
                                itemMatches = true;
                                break;
                            }
                        } catch(err) {
                            console.info(item,prop,item[prop]);
                            console.error('Error',err)
                        }
                    }

                    if (itemMatches) {
                        out.push(item);
                    }
                });
            }
            else {
                // Let the output be the input untouched
                out = items;
            }

            return out;
        };
    }

})();
/* global angular */
(function() {
    'use strict';

    angular
        .module('CarreEntrySystem')
        .directive('uiSrefActiveIf', uiSrefActiveIfDirective)
        .directive('countTo', countToDirective);

    /** @ngInject */
    function uiSrefActiveIfDirective($state) {
        return {
            restrict: "A",
            controller: ['$scope', '$element', '$attrs', 'CONFIG', function($scope, $element, $attrs, CONFIG) {
                
                var menuColor=function(state){
                    var elem=state.substr(state.lastIndexOf('.')+1);
                    console.log(elem);
                    var color="";
                    switch (elem) {
                        case 'risk_factors':
                            color=CONFIG.COLORS[0];
                            break;
                        case 'risk_evidences':
                            color=CONFIG.COLORS[1];
                            break;
                        case 'risk_elements':
                            color=CONFIG.COLORS[2];
                            break;
                        case 'observables':
                            color=CONFIG.COLORS[3];
                            break;
                        case 'citations':
                            color=CONFIG.COLORS[4];
                            break;
                        
                        default:
                            color="#EEEEEE";
                    }
                    return color;
                }
                function update() {
                    var state = $attrs.uiSrefActiveIf;
                    if ($state.includes(state) || $state.is(state)) {
                            // console.log(state,$state.includes(state),$state.is(state))
                            $element.css("border-right","5px solid "+menuColor(state))
                            $element.addClass("active");
                    }
                    else {
                        // angular.element("#side-menu li").each(function() {
                        //   $( this ).removeClass("active");
                        // });
                        $element.css("border-right","none");
                        $element.removeClass("active");
                    }
                }

                $scope.$on('$stateChangeSuccess', update);
                update();
            }]
        };
    }
    
    /** @ngInject */
    function countToDirective($timeout) {
        return {
            replace: false,
            scope: true,
            link: function (scope, element, attrs) {

                var e = element[0];
                var num, refreshInterval, duration, steps, step, countTo, value, increment;

                var calculate = function () {
                    refreshInterval = 30;
                    step = 0;
                    scope.timoutId = null;
                    countTo = parseInt(attrs.countTo) || 0;
                    scope.value = parseInt(attrs.value, 10) || 0;
                    duration = (parseFloat(attrs.duration) * 1000) || 0;

                    steps = Math.ceil(duration / refreshInterval);
                    increment = ((countTo - scope.value) / steps);
                    num = scope.value;
                };

                var tick = function () {
                    scope.timoutId = $timeout(function () {
                        num += increment;
                        step++;
                        if (step >= steps) {
                            $timeout.cancel(scope.timoutId);
                            num = countTo;
                            e.textContent = countTo;
                        } else {
                            e.textContent = Math.round(num);
                            tick();
                        }
                    }, refreshInterval);

                };

                var start = function () {
                    if (scope.timoutId) {
                        $timeout.cancel(scope.timoutId);
                    }
                    calculate();
                    tick();
                };

                attrs.$observe('countTo', function (val) {
                    if (val) {
                        start();
                    }
                });

                attrs.$observe('value', function (val) {
                    start();
                });

                return true;
            }
        };
    }
        
})();

(function() {
  'use strict';

  angular
    .module('CarreEntrySystem')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, toastr, $location, CONFIG,CARRE,$state,SweetAlert, Tutorial,$scope,$rootScope) {
    var vm = this;
    
    CONFIG.ROOT_URL=rootUrl();
    vm.config = CONFIG;
    vm.user = vm.config.currentUser || {};
    
    //set up the urls 
    vm.loginUrl = CONFIG.CARRE_DEVICES + 'login?next=';
    vm.logoutUrl = CONFIG.CARRE_DEVICES + 'logout?next=';
    vm.settingsUrl = CONFIG.CARRE_DEVICES + 'settings';
    vm.passwordUrl = CONFIG.CARRE_DEVICES + 'recover?next=';

    //show message for the user
    if( !$rootScope.isEmbedded ) {
      if(vm.user.username){
        toastr.success('Have fun with the risk factors!','<h4>Hi '+vm.user.username+'!</h4>');
      } else {
        toastr.info('Please login if you want to add/edit data.','<h4>Hello Guest!</h4>');
      }
    }
    
    function rootUrl(){
      if(CONFIG.ENV==='DEV'){
        return window.location.host+'/#/';
      } else if(CONFIG.ENV==='PROD'){
        return window.location.host+'/';
      }
    }
    
    vm.deleteCurrent = function(id) {
      if(!id) return;
      SweetAlert.swal({
          title: "Are you sure?",
          text: "Your will not be able to recover this element!",
          type: "warning",
          showCancelButton: true,
          confirmButtonColor: "#DD6B55",
          confirmButtonText: "Yes, delete it!",
          closeOnConfirm: true,
          closeOnCancel: true
        },
        function(isConfirm) {
          if (isConfirm) { 
            if(CONFIG.AllowDelete) {
              CARRE.delete(id).then(function() { $state.go('^.list'); }); 
            } else {
              toastr.info(id+' deleted!','DEBUG');
              $timeout(function(){ $state.go('^.list') },500);
            }
          }
        });
    };
    
    $scope.IntroOptions = Tutorial.options();
    
  
    $timeout(function(){
      if(!CONFIG.currentUser.username && CONFIG.ENV==='DEV' &&  $state.includes("main.dashboard") ) Tutorial.startGuest($scope.startIntro);
    },1000)
    

  }
})();

(function() {
    'use strict';

    angular
        .module('CarreEntrySystem')
        .run(runBlock)
        .run(setPermissionValidator);

    /** @ngInject */
    function setPermissionValidator(Permission, Auth, $q) {
        // Define anonymous role
        console.log('Role Definition called!');
        Permission.defineRole('authenticated_user', function(stateParams) {
            return Auth.isLoggedIn();
        });
    }

    /** @ngInject */
    function runBlock($rootScope,$state,$location,CONFIG,$http,$cookies) {
            

        // check if it is embeded
        if($location.search().embed) {
            
          $rootScope.isEmbedded = true;
          if($location.search().hidemenu) $rootScope.hideMenu = true;
          if($location.search().showonlygraph) $rootScope.showOnlyGraph = true;
          
        }
        
        // handle ui-router error redirect
        if(CONFIG.useOfflineMode) {
            $rootScope.$on('$stateChangeStart', function(event, toState, toParams, fromState, fromParams, options){
                if(toState.name==='500_API_ERROR') { 
                    event.preventDefault(); 
                    $rootScope.isOffline = true; 
                }
            });
        }

        //handle ui-router errors
        $rootScope.$on('$stateChangeError', function(event, toState, toParams, fromState, fromParams, error) {
            // this is required if you want to prevent the $UrlRouter reverting the URL to the previous valid location
            event.preventDefault();
            console.error('Ui Router error:', event, toState, toParams, fromState, fromParams, error);
            console.log(error);
            // $state.go(toState.name,toParams.id);
        });

        // window.hexToRgb = function(hex) {
        //     var shorthandRegex = /^#?([a-f\d])([a-f\d])([a-f\d])$/i;
        //     hex = hex.replace(shorthandRegex, function(m, r, g, b) {
        //         return r + r + g + g + b + b;
        //     });

        //     var result = /^#?([a-f\d]{2})([a-f\d]{2})([a-f\d]{2})$/i.exec(hex);
        //     return result ? {
        //         r: parseInt(result[1], 16),
        //         g: parseInt(result[2], 16),
        //         b: parseInt(result[3], 16)
        //     } : null;
        // }
        
        
        // allow language change hack
        var lang_cookie = $cookies.get('CARRE_LANG')
        CONFIG.LANG = lang_cookie&&lang_cookie.length===2?lang_cookie:"en";
        if($location.search().lang) {
          CONFIG.LANG = $location.search().lang;
        }
        
        // load language file
        window.CARRE_TRANSLATIONS = {'el':{},'lt':{},'en':{}};
        $http.get('/translation.'+CONFIG.LANG+'.json',{cache:true}).then(function(res){
            console.debug(res);
            window.CARRE_TRANSLATIONS[CONFIG.LANG] = res.data;
        });

    }

})();


