$templateCache.put("app/about/about.html","<h2 class=\"header\">More Information</h2><div><iframe src=\"//www.slideshare.net/slideshow/embed_code/key/rL6KQLdZuhK9Xu?startSlide=2\" width=\"595\" height=\"485\" frameborder=\"0\" marginwidth=\"0\" marginheight=\"0\" scrolling=\"no\" style=\"border:1px solid #CCC; border-width:1px; margin-bottom:5px; max-width: 100%;\" allowfullscreen=\"\"></iframe><p>The CARRE risk entry system enables the <a href=\"http://entry.carre-project.eu/medical_experts\">medical experts</a> in the <a href=\"http://www.carre-project.eu/\">CARRE project</a> to encode the risk factors between biological, demographic, lifestyle and environmental elements and clinical outcomes in accordance with evidence from the clinical literature. The presented system is based fundamentally on <a href=\"http://www.w3.org/DesignIssues/LinkedData.html\"></a>Linked Data principles, and so in order to make the best use of these encoded factors, they are available as Linked Data through the <a href=\"http://carre.kmi.open.ac.uk:8890/sparql\">CARRE SPARQL Endpoint</a>, making use of control vocabularies (<a href=\"https://www.nlm.nih.gov/research/umls/\">UMLS</a> , <a href=\"https://www.nlm.nih.gov/snomed/\"></a>SNOMED, etc.) and the <a href=\"http://bioportal.bioontology.org/ontologies/CARRE\">CARRE risk factor ontology</a>; specifically, they are encoded in the (standard) Resource Description Framework (<a href=\"http://www.w3.org/RDF/\">RDF</a>) format.</p><p>The system has been developed as a client side web application that follows the <a href=\"http://microservices.io/\">Microservices</a> architectural style and provides native RDF support via the <a href=\"https://carre.kmi.open.ac.uk/ws/\">CARRE API</a> and the <a href=\"http://carre.kmi.open.ac.uk:8890/sparql\">CARRE SPARQL Endpoint</a>. Moreover custom form components have been developed to reflect the structure of the <a href=\"http://bioportal.bioontology.org/ontologies/CARRE\">CARRE risk factor ontology</a>, so that citations, observables, risk elements, risk evidences and risk factors are automatically stored into the CARRE RDF triple store. The CARRE risk entry system is implemented in <a href=\"http://www.w3.org/TR/html5/\">HTML5</a>, <a href=\"http://www.w3.org/Style/CSS/\"></a>CSS and <a href=\"https://www.javascript.com/\">JavaScript</a> using the <a href=\"https://angularjs.org/\">AngularJS</a> framework. The system is open source and the source code is available at <a href=\"https://github.com/carre-project/carre-entry-system\">GitHub</a>. The system maintains referential integrity, so that if “diabetes” is entered as a risk element entity, then a risk factor representing an observed link between diabetes and hypertension will refer to the existing diabetes risk element entity. Medical experts are supported in the reuse of data already entered into the system by the user interface, which allows existing relevant entities to be selected via appropriate searchable lists wherever possible.</p><p>Web development: <a href=\"http://nporto.com/\">Nick Portokallidis</a></p><p>Data cleansing: <a href=\"http://drosatos.info/\">George Drosatos</a></p><p>Scientific supervision: <a href=\"http://iris.med.duth.gr/kaldoudi/en/\">Eleni Kaldoudi</a></p></div><br><br><footer class=\"footer\" style=\"height:60px;\"><div class=\"row\"><div class=\"col-xs-6\"><span><a href=\"//www.carre-project.eu\" target=\"_blank\"><img src=\"assets/images/CARRE-FooterLogo.png\" width=\"36\" height=\"40\" alt=\"CARRE\">&nbsp;CARRE Project © 2015</a></span></div><div class=\"col-xs-6\"><span class=\"pull-right\" style=\"\">FP7-ICT-611140.&nbsp; <a href=\"//cordis.europa.eu/\" target=\"_blank\"><img src=\"assets/images/Picture1-150x150.png\" width=\"40\" height=\"40\" alt=\"EU\"></a></span></div></div></footer>");
